import React from 'react';
import './Hero.css'; 

const Hero = () => {
  return (
    <section className="hero" id="home">
      <div className="container">
        <h1 className="hero-title">Why Web Development Matters</h1>
        <p className="hero-subtitle">Your website is the cornerstone of your online presence. Make it count.</p>
        <a href="#importance" className="btn-primary">Learn More</a>
      </div>
    </section>
  );
};

export default Hero;
