// src/components/Features.js
import React from 'react';
import './Features.css'; // Import a CSS file for styling

const Features = () => {
  return (
    <section id="importance" class="importance">
        <h2>The Importance of Web Development</h2>
        <div class="importance-items">
            <div class="importance-item">
                <i class="fas fa-rocket fa-3x"></i>
                <h3>Performance</h3>
                <p>A fast and responsive website keeps users engaged and reduces bounce rates.</p>
            </div>
            <div class="importance-item">
                <i class="fas fa-shield-alt fa-3x"></i>
                <h3>Security</h3>
                <p>Protecting your users' data builds trust and ensures compliance with regulations.</p>
            </div>
            <div class="importance-item">
                <i class="fas fa-user-friends fa-3x"></i>
                <h3>User Experience</h3>
                <p>Intuitive design and seamless navigation enhance user satisfaction and conversion rates.</p>
            </div>
            <div class="importance-item">
                <i class="fas fa-chart-line fa-3x"></i>
                <h3>Business Impact</h3>
                <p>A well-designed website can significantly boost your brand's visibility and credibility.</p>
            </div>
        </div>
    </section>

  );
};

export default Features;
